<script>
import { chinaMapData, infoData } from './china';

export default {
  name: 'ECHARTS',
  data() {
    return {
      currentTime: '', //当前时间
      currentIndex: 0, //当前标识
      infoData, //发展历程数据
      scrollInterval: null, //滚动定时器
    };
  },
  created() {
    this.getCurrentTime(); //初始化获取时间
    setInterval(this.getCurrentTime, 1000); // 每隔1秒更新时间
  },
