    /**
     * 设置滚动
     */
    setScroll() {                
      const scrollList = this.$refs.scrollList;
      const liHeight = scrollList.querySelector('li').offsetHeight;
      const totalHeight = liHeight * this.infoData.length;
      this.scrollInterval = setInterval(() => {
        this.currentIndex++;
        if (this.currentIndex * liHeight >= totalHeight) {
          this.currentIndex = 0;
        }
        scrollList.style.transform = `translateY(-${
          this.currentIndex * liHeight
        }px)`;
      }, 1000); // 滚动间隔时间，单位毫秒
    },
    /**
     * 设置填充0
     */
    setFillZero(str) {
      if (str < 10) {
        return '0' + str;
      } else {
        return str;
      }
    },
    /**
     * 获取当前时间
     */
    getCurrentTime() {
      let currentDate = new Date();
      let currentYear = currentDate.getFullYear();
      let currentMonth = currentDate.getMonth() + 1;
      let currentToday = currentDate.getDate();
      let currentDay = currentDate.getDay();
      let currentHour = currentDate.getHours();
      let currentMinute = currentDate.getMinutes();
      let currentSecond = currentDate.getSeconds();
      let week = [
        '星期日',
        '星期一',
        '星期二',
        '星期三',
        '星期四',
        '星期五',
        '星期六',
      ];

      let nowTime =
        currentYear +
        '-' +
        this.setFillZero(currentMonth) +
        '-' +
        this.setFillZero(currentToday) +
        '  ' +
        this.setFillZero(currentHour) +
        ':' +
        this.setFillZero(currentMinute) +
        ':' +
        this.setFillZero(currentSecond) +
        '  ' +
        week[currentDay] +
        '  ';
      this.currentTime = nowTime;
    },
